.hero {
    position: relative;
}

.hero h3 {
    font-weight: 100;
    letter-spacing: 2px;
}

.hero h1 {
    font-style: 60px;
    font-weight: bold;
}

.hero h1 span {
    color: #ff014f
}

.hero h2 {
    font-size: 50px;
    font-weight: bold;
    color: #ff014f;
}

.hero h2 span {
    color: black;
}

.hero p {
    color: #878e99;
    font-size: 18px;
    line-height: 30px;
    margin-top: 30px;
}

.hero_btn {
    margin-top: 10%;
}

.hero_btn h4 {
    font-weight: 500;
    letter-spacing: 2px;
    color: #43474b;
    font-size: 15px;
    margin: 20px 0 20px 0;
}

.hero_btn button {
    margin-right: 30px;
}

.hero_btn button img {
    width: 20px;
    height: 20px;
}

.hero .left {
    margin-right: 10%;
}

.hero .right {
    width: 40%;
    position: relative;
}

.hero .right_img {
    background: linear-gradient(145deg, #e2e8ec, #ffffff);
    box-shadow: 4px 4px 8px #cbcbcb, -4px -4px 8px #ffffff;
    border-radius: 10px;
    height: 470px;
    width: 510px;
    margin-top: 20%;
}

.hero .right_img img {
    position: absolute;
    top: 0;
    right: 10%;
    width: 510px;
    height: 580px;
}

@media (max-width: 1200px) {
    .hero .right {
        width: 65%;
        position: relative;
    }

    .hero_btn button {
        margin-right: 5px;
    }
}

@media (max-width: 770px) {

    .hero .right,
    .hero .left {
        width: 100%;
        margin-left: 20px;
    }

    .hero .f_flex {
        flex-direction: column;
        flex-direction: column-reverse;
    }

    .hero .right_img {
        width: 98%;
        height: 60vh;
    }

    .hero .right_img img {
        height: 75vh;
    }
}

@media (max-width: 600px) {
    .hero .right_img {
        display: none;
    }

    .hero_btn button {
        margin-right: 10px;
    }

    .hero_btn button img {
        width: 10px;
        height: 10px;
    }
}

@media(max-width:480px) {
    .hero .top {
        margin-top: 30px;
    }

    .hero .right,
    .hero .left {
        width: 100%;
        margin-left: 10px;
    }

    .hero_btn {
        display: flex;
        flex-direction: column;
    }

    .hero h3 {
        font-size: 1.2rem;
    }

    .hero h1 {
        font-size: 1rem;
    }

    .hero h2 {
        font-size: 1.5rem;
    }

    .hero p {
        font-size: .8rem;
    }
}