.Testimonial h2 {
    margin: 15px 0 0 0;
}

.Testimonial .left {
    width: 35%;
    padding: 35px;
    margin: 20px;
}

.Testimonial .left img {
    height: 250px;
}

.Testimonial .right {
    width: 65%;
    padding: 0 0 0 50px;
    margin: 0 20px 0 0;
}

.Testimonial .quote i {
    font-size: 150px;
    opacity: 0.1;
}

.Testimonial .content {
    padding: 50px;
}

.Testimonial .content h1 {
    font-weight: 500;
}

.Testimonial .content h3 {
    font-size: 18px;
    font-weight: 400;
    margin-bottom: 40px;
}

.Testimonial .slide_button {
    position: absolute;
    top: 30px;
    right: 20px;
}

.Testimonial .slide_button .prev_btn {
    margin-right: 30px;
}

/*-----Testimonial slide-----------*/
.Testimonial .slide {
    max-width: 80%;
    margin: auto;
    height: 500px;
    position: relative;
    display: flex;
    overflow: hidden;
}

article {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: all 0.3s linear;
}

article.activeSlide {
    opacity: 1;
    transform: translateX(0);
}

article.lastSlide {
    transform: translateX(-100%);
}

article.nextSlide {
    transform: translateX(100%);
}

/*-----Testimonial slide-----------*/
@media (max-width: 768px) {
    article {
        flex-direction: column;
        padding: 20px;
    }

    .Testimonial .left {
        width: 100%;
        display: flex;
        margin: 0;
        margin-top: 15%;
    }

    .Testimonial .left img {
        width: 190px;
        border-radius: 10px;
        margin-right: 20px;
    }

    .Testimonial .right {
        width: 100%;
        margin: 0;
        padding: 0 0 0 0px;
    }

    .Testimonial .slide {
        height: 80vh;
        max-width: 90%;
    }

    .Testimonial .quote {
        display: none;
    }
}

@media (max-width :480px) {
    article {
        padding: 0;
    }

    .Testimonial .heading {
        padding: 0;
        margin: 30px 0 0 0;
    }

    .Testimonial h1 {
        font-size: 2rem;
    }

    .Testimonial .left {
        height: 40vh;
        margin-top: 90px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .Testimonial .left .details {
        font-size: 1rem;
        margin-top: .8rem;
    }


    .Testimonial .left img {
        height: 120px;
        width: 120px;
        object-fit: cover;
        overflow: hidden;
    }

    .Testimonial .slide {
        height: 100vh;
        max-width: 90%;
    }

    .Testimonial .button {
        font-size: 8px;
    }

    .Testimonial .content {
        padding: 1rem;
    }

    .Testimonial .content h1 {
        font-size: 1.5rem;
    }

    .Testimonial .content h3 {
        font-size: .8rem;
    }

    .Testimonial .content p {
        font-size: 1rem;
        margin-top: -2rem;
    }


}