.Resume .heading h1 {
    font-size: 2.5rem;
}

.Resume .content {
    max-width: 85%;
    margin: auto;
}

.Resume .box {
    padding: 2.5rem;
}

.Resume hr {
    margin: 2rem 0 2rem 0;
    border: 1px solid #e3e9ed;
    background-color: #e3e9ed;
}

.Resume .rate .btn_shadow {
    color: #ff014f;
    padding: 1rem 1.5rem;
    font-weight: bold;
    transition: 0.5s;
}

.Resume p {
    font-size: 1.1rem;
}

.Resume .box {
    margin-bottom: 2.5rem;
    position: relative;
}

.Resume .btn_shadow:hover {
    transform: translate(0);
}

.Resume .box:hover .btn_shadow {
    background: #ff3a75;
    box-shadow: none;
    color: white;
}

.Resume .box:hover p {
    color: white;
}

.Resume .content-section {
    position: relative;
}

.Resume .left {
    position: relative;
}

.Resume .left::after {
    content: "";
    position: absolute;
    top: 16%;
    left: 0;
    height: 125vh;
    width: 5px;
    background: #d9dee2;
    z-index: -2;
}

.Resume .box::after {
    content: "";
    position: absolute;
    top: 45%;
    left: -8%;
    height: 5px;
    width: 50px;
    background: #d9dee2;
}

.Resume .box::before {
    content: "";
    position: absolute;
    top: 42%;
    left: -10%;
    height: 10px;
    width: 10px;
    background: #fff;
    border: 5px solid #d9dee2;
    border-radius: 50%;
    z-index: 5;
    transition: 0.5s;
}

.Resume .box:hover.box:before {
    background: #ff3a75;
}


@media (max-width: 768px) {
    .Resume .d_flex {
        flex-direction: column;
    }

    .Resume .left::after {
        top: 14%;
        height: 105vh;
    }
}

@media (max-width:480px){
    .Resume .content {
        max-width: 90%;
        margin: auto;
    }
    .Resume .heading h1 {
        font-size: 2rem;
    }

    .Resume .box {
        padding: 1rem;
    }

    .Resume .title {
        font-size: .7rem;
        margin-bottom: .5rem;
    }

    .Resume hr {
        margin: 1rem 0 1rem 0;
    }

    .Resume .rate .btn_shadow {
        padding: .5rem .5rem;
    }

    .Resume p {
        font-size: .8rem;
    }

    .Resume .left::after {
        top: 18%;
        height: 114vh;
    }
    .Resume .box::after {
        top: 49%;
        left: -5%;
        width: 20px;
    }
    .Resume .box::before {
        top: 46%;
        left: -8%;
    }
}